import React, { Component } from "react";
import { webServiceKey } from "../data";
import logo from "../food.svg";

class Header extends Component {
  state = {
    weather: null
  };

  /**
   * 
   *
   * @param {string} weather
   * @returns
   * @memberof Header
   */
  getWeatherIcon(weather) {
    switch (weather) {
      case "":
        return "icon-qing1";
      case "":
        return "icon-duoyun";
      case "":
        return "icon-yin";
      case "":
        return "icon-zhenyu";
      case "":
        return "icon-leizhenyu1";
      case "":
        return "icon-leizhenyubanbingbao1";
      case "":
        return "icon-xiaoyu";
      case "":
        return "icon-zhongyu";
      case "":
        return "icon-dayu1";
      case "":
        return "icon-baoyu1";
      case "":
        return "icon-dabaoyu1";
      case "":
        return "icon-tedabaoyu";
      case "":
        return "icon-zhenxue";
      case "":
        return "icon-xiaoxue";
      case "":
        return "icon-zhongxue";
      case "":
        return "icon-daxue1";
      case "":
        return "icon-baoxue1";
      case "":
        return "icon-wu";
      case "":
        return "icon-dongyu";
      case "":
        return "icon-shachenbao1";
      case "":
        return "icon-fuchen1";
      case "":
        return "icon-yangsha";
      case "":
        return "icon-qiangshachenbao1";
      case "":
        return "icon-biao";
      case "":
        return "icon-tianqizitiku18";
      case "":
        return "icon-wu";
      case "":
        return "icon-mai1";
      default:
        return "";
    }
  }

  /**
   * 
   *
   * @param {string} adcode
   * @memberof Header
   */
  getWeatherInfo(adcode) {
    const url = `https://restapi.amap.com/v3/weather/weatherInfo?key=${webServiceKey}&city=${adcode}`;
    fetch(url)
      .then(res => res.json())
      .then(data => {
        if (data.info === "OK" && data.infocode === "10000") {
          this.setState({
            weather: data.lives[0]
          });
        }
      });
  }

  componentWillReceiveProps({ cityCode }) {
    if (!this.state.weather && cityCode) {
      // 
      this.getWeatherInfo(cityCode);
    }
  }

  render() {
    let { weather } = this.state;

    return (
      <div>
        <header className="header">
          <div className="header-left">
            <img src={logo} alt="logo" className="logo" />
            <h1 className="title"></h1>
          </div>
          {weather ? (
            <div className="weather">
              {this.getWeatherIcon(weather.weather) ? (
                <i
                  className={`iconfont ${this.getWeatherIcon(weather.weather)}`}
                />
              ) : (
                <p>{weather.weather}</p>
              )}
              <p>{weather.temperature}</p>
            </div>
          ) : (
            ""
          )}
        </header>
      </div>
    );
  }
}

Header.defaultProps = {
  cityCode: ""
};

export default Header;



